import $ from 'jquery';

// DOM selectors
const POST_EDIT_PAGE = '.wp-admin.post-php,.wp-admin.post-new-php';
const METABOX = '#wptelegram_pro_p2tg_override';

const SEND2TG = '#_wptgpro_p2tg_send2tg';
const SEND2TG_DEPENDENT_FIELDS = '#_wptgpro_p2tg_force_send-label';

const OVERRIDE_SWITCH = 'input[type="checkbox"][name="_wptgpro_p2tg_[override_switch]"]';
const OVERRIDE_DEPENDENT_FIELDS = '[name^="_wptgpro_p2tg_[instances]"]';

const INSTANCES = '[name^="_wptgpro_p2tg_[instances]"][name$="[active]"]';

$(() => {
	const page = $(POST_EDIT_PAGE);

	// Send to Telegram switch
	page.on('change', SEND2TG, function () {
		const fields = page.find(SEND2TG_DEPENDENT_FIELDS);

		if ($(this).is(':checked')) {
			fields.show();
		} else {
			fields.hide();
		}
	});
	page.find(SEND2TG).trigger('change');

	const metabox = page.find(METABOX);

	// Override settings switch
	metabox.on('change', OVERRIDE_SWITCH, function () {
		const fields = metabox.find(OVERRIDE_DEPENDENT_FIELDS).closest('.cmb-row');

		if ($(this).is(':checked')) {
			fields.show(300);
			metabox.find(INSTANCES).trigger('change', [true]);
		} else {
			fields.hide(300);
		}
	});
	metabox.find(OVERRIDE_SWITCH).trigger('change');

	// Instances switch
	metabox.on('change', INSTANCES, function (event, noAnimation) {
		// name can be something like "_wptgpro_p2tg_[instances][498][active]", So instance ID will be 498
		const instanceId = (this.name as string)?.replace('_wptgpro_p2tg_', '')?.replace(/[^0-9]/g, '');

		if (!instanceId) {
			return;
		}
		const duration = noAnimation ? undefined : 300;

		// All fields without the instance active field
		const instanceFields = $(`[name^="_wptgpro_p2tg_[instances][${instanceId}]"]`).not('[name$="[active]"]');
		// The CMB2 rows
		const rows = instanceFields.closest('.cmb-row');

		if ($(this).is(':checked')) {
			rows.show(duration);
		} else {
			rows.hide(duration);
		}
	});
});
